
import { mapState } from 'vuex'
import $ from 'jquery'
import { AjaxHttp, AjaxImageHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, isArrayType, getImageUrl, isNullValue, getYMDHMFormateTime, copyObject, compressImage, createBlob, openFile } from "../../../js/utility.js"
import ListCell from '../../crm/components/ListCell.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'

import event from '../../../js/Event.js'
import PlateType from '../../../js/enum.js'

export default {
name: 'taskview',
components: {
ListCell,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XTextarea,
Popup,
Actionsheet,
Tab,
TabItem,
PopupPicker,
Group
},
computed: {
...mapState([
'taskList',
'taskDetail'
]),
popupTitle() {

if (this.popupType === 1 || this.popupType === 3) {
return '添加';
} else if (this.popupType === 2 || this.popupType === 4) {
return '编辑';
}
},
popupPlaceholder() {
if (this.popupType === 1 || this.popupType === 2) {
return '请填写描述';
} else if (this.popupType === 3 || this.popupType === 4) {
return '请填写子任务';
}
},
priorityColor() {
if (this.priority === '高') {
return '#f96868';
} else if (this.priority === '普通') {
return '#46be8a';
} else if (this.priority === '低') {
return '#f2a654';
}
return '#FFF';
},
statusColor() {
if (this.status === '未启动') {
return '#ed5565';
} else if (this.status === '推迟') {
return '#f8ac59';
} else if (this.status === '进行中') {
return '#1ab394';
} else if (this.status === '完成') {
return '#00aaef';
}
return '#FFF';
},
},
data() {
return {
taskDataDetail: '',
subject: '',
create_date: '',
sub_count: '',
done_count: '',
module_name: '',
module_id: '',
sub_list: [], //子任务数组
file_list: [], //附件数组
comment_list: [], //评论数组
action_list: [], //活动数组  type 7 8 9 10 role_list 有数据
owner_roles_list: [], //分配人
about_roles_list: [], //关注人
status_color: '#999',
status: '',
priority: '',
description: '', //描述

addPopupShow: false, //弹窗操作所需信息
popupContent: '',
popupType: 1, //1 添加描述  2 添加子任务 3 编辑子任务
popupItem: null,

showRightMenus: false, //编辑 删除操作
rightMenus: {},
permission: false,

canEdit: false,

tab_type: 'reply',
showStatusPopupPicker: false,
showPriorityPopupPicker: false,
statusPickerValue: [],
priorityPickerValue: [],
}
},
beforeRouteLeave(to, from, next) {
if (to.name === 'taskhandle' || to.name === 'createcomment' || to.name === 'filepreview' || to.name === 'relatelist' || to.name === 'user' || to.name === 'selectlist' || to.name === 'maindetail') {
this.$store.commit('updateTaskDetail', {
type: 'taskview',
id: this.$route.params.id,
taskDataDetail: this.taskDataDetail,
subject: this.subject,
create_date: this.create_date,
sub_count: this.sub_count,
done_count: this.done_count,
sub_list: this.sub_list,
file_list: this.file_list,
module_name: this.module_name,
module_id: this.module_id,
comment_list: this.comment_list,
action_list: this.action_list,
owner_roles_list: this.owner_roles_list,
about_roles_list: this.about_roles_list,
status_color: this.status_color,
status: this.status,
taskpriority: this.priority,
statusPickerValue: this.statusPickerValue,
priorityPickerValue: this.priorityPickerValue,
description: this.description,
rightMenus: this.rightMenus,
permission: this.permission,
canEdit: this.canEdit,
scroll: $('#scroll_area').scrollTop(),
refresh: false, //刷新详情
});
event.$on('selectedRelateItem', function(data) {
this.handleSelectedInfo(data);
}.bind(this));
} else {
this.$store.commit('updateTaskDetail', {});
}

next();
},
beforeRouteEnter(to, from, next) {
if (from.name === 'relatelist') {
event.$off("selectedRelateItem");
}
next();
},
beforeDestroy() {
event.$off("onClickMore");
},

mounted() {
event.$on('onClickMore', function(path) {
this.showRightMenus = true;
}.bind(this));

if (this.taskDetail.type === 'taskview' && this.taskDetail.id === this.$route.params.id && !this.taskDetail.refresh) {
this.taskDataDetail = this.taskDetail.taskDataDetail;
this.subject = this.taskDetail.subject;
this.create_date = this.taskDetail.create_date;
this.sub_count = this.taskDetail.sub_count;
this.done_count = this.taskDetail.done_count;
this.sub_list = this.taskDetail.sub_list;
this.file_list = this.taskDetail.file_list;
this.module_name = this.taskDetail.module_name;
this.module_id = this.taskDetail.module_id;
this.comment_list = this.taskDetail.comment_list;
this.action_list = this.taskDetail.action_list;
this.owner_roles_list = this.taskDetail.owner_roles_list;
this.about_roles_list = this.taskDetail.about_roles_list;
this.status_color = this.taskDetail.status_color;
this.status = this.taskDetail.status;
this.priority = this.taskDetail.taskpriority;
this.statusPickerValue = this.taskDetail.statusPickerValue;
this.priorityPickerValue = this.taskDetail.priorityPickerValue;
this.description = this.taskDetail.description;

this.rightMenus = this.taskDetail.rightMenus;
this.permission = this.taskDetail.permission;
this.canEdit = this.taskDetail.canEdit;

if (this.permission) {
this.$store.commit('updateXheaderRightOptions', true);
}
this.$nextTick(() => {
$('#scroll_area').scrollTop(this.taskDetail.scroll); 
});


if (this.taskDetail.editCustomer) {
AjaxHttp('POST', 'm=task&a=edit', { id: this.$route.params.id, module: 'customer', module_id: this.taskDetail.temp_module_id }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.module_name = wself.taskDetail.temp_module_name;
wself.module_id = wself.taskDetail.temp_module_id;
}
});
}
if (this.taskDetail.editOwner) {
var owner_role_id = '';
for (var i = 0; i < this.taskDetail.temp_owner_roles_list.length; i++) {
if (i === 0) {
owner_role_id = ',' + this.taskDetail.temp_owner_roles_list[i].role_id + ',';
} else {
owner_role_id = owner_role_id + this.taskDetail.temp_owner_roles_list[i].role_id + ','
}
}
AjaxHttp('POST', 'm=task&a=edit', { id: this.$route.params.id, owner_role_id: owner_role_id }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.owner_roles_list = copyObject(wself.taskDetail.temp_owner_roles_list);
}
});
}
if (this.taskDetail.editAbout) {
var about_role_id = '';
for (var i = 0; i < this.taskDetail.temp_about_roles_list.length; i++) {
if (i === 0) {
about_role_id = ',' + this.taskDetail.temp_about_roles_list[i].role_id + ',';
} else {
about_role_id = about_role_id + this.taskDetail.temp_about_roles_list[i].role_id + ','
}
}
AjaxHttp('POST', 'm=task&a=edit', { id: this.$route.params.id, about_role_id: about_role_id }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.about_roles_list = copyObject(wself.taskDetail.temp_about_roles_list);
}
});
}


} else {
this.getDetail();
}
},
methods: {
getDetail() {
AjaxHttp('POST', 'm=task&a=view', { id: this.$route.params.id }, this, function(res, wself, success) {
if (success) {
var permission = res.permission;
if (permission.edit === 1 || permission.delete === 1) {
wself.$store.commit('updateXheaderRightOptions', true);
wself.permission = true;
if (permission.edit === 1) { //商机和合同的编辑 暂时未实现
wself.rightMenus.edit = '编辑';
wself.canEdit = true;
}
if (permission.delete === 1) {
wself.rightMenus.delete = '归档';
}
}

wself.taskDataDetail = res.data;

wself.subject = res.data.subject;
wself.create_date = res.data.create_date;
wself.sub_count = res.data.sub_count;
wself.done_count = res.data.done_count;

wself.sub_list = res.data.sub_list;
wself.file_list = res.data.file_list;
wself.module_name = res.data.module_name;
wself.module_id = res.data.module_id;


var comment_list = [];
for (var i = 0; i < res.data.comment_list.length; i++) {
var item = res.data.comment_list[i];
item.img = getImageUrl(item.img);
item.create_time = getYMDHMFormateTime(item.create_time);

for (var j = 0; j < item.comment_list_child.length; j++) {
var childitem = item.comment_list_child[j];
childitem.creator_child.thumb_path = getImageUrl(childitem.creator_child.thumb_path);
childitem.create_time = getYMDHMFormateTime(childitem.create_time);
}
comment_list.push(item);
}
wself.comment_list = comment_list;
wself.action_list = res.data.action_list;

var owner_roles_list = [];
for (var i = 0; i < res.data.owner_roles_list.length; i++) {
var item = res.data.owner_roles_list[i];
item.thumb_path = getImageUrl(item.thumb_path);
owner_roles_list.push(item);
}
wself.owner_roles_list = owner_roles_list;

var about_roles_list = [];
for (var i = 0; i < res.data.about_roles_list.length; i++) {
var item = res.data.about_roles_list[i];
item.thumb_path = getImageUrl(item.thumb_path);
about_roles_list.push(item);
}
wself.about_roles_list = about_roles_list;

wself.status_color = res.data.status_color;
wself.status = res.data.status;
wself.priority = res.data.priority;
wself.statusPickerValue = [wself.status];
wself.priorityPickerValue = [wself.priority];
wself.description = res.data.description;
}
});
},
getTimeContent(time) {
return getYMDFormateTime(time);
},
getImgeSrc(item) {
return getImageUrl(item.thumb_path);
},
getStatusMark(status) {
if (status === '完成') {
return require('../../../assets/selected.png');
}
return require('../../../assets/un_selected.png');
},
isCompleteStatus(status) {
if (status === '完成') {
return true;
}
return false;
},
changeSatus(status) {
if (this.canEdit) {
AjaxHttp('POST', 'm=task&a=edit', { id: this.$route.params.id, status: status === '完成' ? '进行中' : '完成' }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.getDetail();
}
});
}
},






isCompleteSubStatus(item) {
if (item.is_done === '1') {
return true;
}
return false;
},
changeSubSatus(item) {
if (this.canEdit) {
AjaxHttp('POST', 'm=task&a=subedit', { sub_id: item.id, content: item.content, is_done: item.is_done === '1' ? '0' : '1' }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
item.is_done = item.is_done === '1' ? '0' : '1';

}
});
}
},
onSwipeoutClick(item, type, index) { //删除 编辑子项目
if (type === 'edit') {
this.popupType = 4;
this.popupItem = item;
this.popupContent = item.content;
this.addPopupShow = true;
} else if (type === 'delete') {
AjaxHttp('POST', 'm=task&a=subdel', { sub_id: item.id }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.sub_list.splice(index, 1);
wself.addPopupShow = false;
}
});
} else if (type === 'filedelete') {
AjaxHttp('POST', 'm=file&a=delete', { id: item.file_id, 'module': 'task' }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.file_list.splice(index, 1);
}
});
}
},
addDescription() { //增加描述
if (this.canEdit) {
if (this.description === '') {
this.popupType = 1;
this.popupItem = null;
this.popupContent = '';
this.addPopupShow = true;
} else {
this.popupType = 2;
this.popupItem = null;
this.popupContent = this.description;
this.addPopupShow = true;
}
}
},
addSubTask() { //增加子任务
if (this.canEdit) {
this.popupType = 3;
this.popupItem = null;
this.popupContent = '';
this.addPopupShow = true;
}

},
popupSave() {
if (this.popupContent === '') {
this.$vux.toast.text(this.popupPlaceholder, 'top');
}

var url = '';
var postParams = {};
if (this.popupType === 1 || this.popupType === 2) {
url = 'm=task&a=edit';
postParams.description = this.popupContent;
postParams.id = this.$route.params.id;
} else if (this.popupType === 3) {
url = 'm=task&a=addsub';
postParams.content = this.popupContent;
postParams.id = this.$route.params.id;
} else if (this.popupType === 4) {
url = 'm=task&a=subedit';
postParams.content = this.popupContent;
postParams.sub_id = this.popupItem.id;
postParams.is_done = this.popupItem.is_done;
}
AjaxHttp('POST', url, postParams, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
if (wself.popupType === 1 || wself.popupType === 2) { //任务描述编辑
wself.description = wself.popupContent;
} else if (wself.popupType === 4) {
wself.popupItem.content = wself.popupContent;
} else {
wself.getDetail();
}
wself.addPopupShow = false;
}
});
},
clickRightMenu(key) {
if (key === 'edit') {
var about_roles_list = [];


this.$store.commit('updateDetail', {
type: 'taskhandle',
subject: this.subject,
description: this.description,
priority: this.priority,
module_id: this.module_id === '0' ? '' : this.module_id,
module_name: this.module_name,
about_role: this.about_roles_list,
});

this.$router.push({
name: 'taskhandle',
params: {
id: this.$route.params.id,
action: 'edit'
}
});
} else if (key === 'delete') {
AjaxHttp('POST', 'm=task&a=delete', { id: this.$route.params.id }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.deleteMainList();
wself.$router.go(-1);
}
});

}
},
deleteMainList() { //更新上层列表
var itemlist = [];
if (this.taskList.nav_type === 1) {
itemlist = this.taskList.unfinished.itemlist;
} else if (this.taskList.nav_type === 2) {
itemlist = this.taskList.unfinished.itemlist;
}
var index = -1;
for (var i = 0; i < itemlist.length; i++) {
var item = itemlist[i];
if (item.task_id === this.$route.params.id) {
index = i;
}
}

if (index >= 0) {
itemlist.splice(index, 1);
this.$store.commit('updateTaskList', this.taskList);
}
},
onTabClick(index) { //切换tab 回复  附件 活动
if (index === 0) {
this.tab_type = 'reply';

} else if (index === 1) {
this.tab_type = 'file';

} else if (index === 2) {
this.tab_type = 'activity';

}
},
addComment(item, type) { //回复
if (this.taskDataDetail === '') { return; }
if (type === 'child') {
this.$router.push({ //回复小人
name: 'createcomment',
params: {
type: 'taskcomment',
id: item.talk_id,
receiveid: item.creator_child.role_id,
name: item.creator_child.full_name,
}
})
} else if (type === 'big') { //回复人
this.$router.push({
name: 'createcomment',
params: {
type: 'taskcomment',
id: item.talk_id,
receiveid: item.send_role_id,
name: item.user_name,
}
})
} else if (type === 'father') { //回复日志
this.$router.push({
name: 'createcomment',
params: {
type: 'taskdefalut',
id: this.$route.params.id,
receiveid: '0',
name: this.subject,
}
})
}
},
deleteComment(item, index, childindex, type) {
AjaxHttp('POST', 'm=task&a=replydel', { talk_id: item.talk_id }, this, function(res, wself, success) {
if (success) {
var item = wself.comment_list[index];
if (type === 'child') {
item.comment_list_child.splice(childindex, 1);
} else {
wself.comment_list.splice(index, 1);
}
}
});
},
addFile() { //附件
if ($(fileElem)) {
$(fileElem).click();
}
},
uploadimg(e) {
var src, url = window.URL || window.webkitURL || window.mozURL,
files = e.target.files;
for (var i = 0, len = files.length; i < len; ++i) {
var file = files[i];






var self = this;
compressImage(file, 0.3, (code) => {
var formdata = new FormData();
formdata.append('file', createBlob(code), file.name);
formdata.append('module', 'task');
formdata.append('r', 'rTaskFile');
formdata.append('id', self.$route.params.id);
AjaxImageHttp('POST', 'm=file&a=add', formdata, self, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.getDetail();
}
});
});

}
},
statusPopupPickerHide(type) { //状态隐藏
if (type) {
AjaxHttp('POST', 'm=task&a=edit', { id: this.$route.params.id, status: this.statusPickerValue[0] }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.status = wself.statusPickerValue[0];
}
});
}
},
priorityPopupPickerHide(type) { //优先级隐藏
if (type) {
var apriority = this.priorityPickerValue[0];
AjaxHttp('POST', 'm=task&a=edit', { id: this.$route.params.id, priority: apriority }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.priority = apriority;
wself.taskDetail.taskpriority = apriority;
wself.$store.commit('updateTaskDetail', wself.taskDetail);

}
});
}
},
fileTypeImage(item) {
var type = item.file_type;
if (type === 'doc' || type === 'DOC' || type === 'docx') {
return require("../../../assets/file/doc.png");
} else if (type === 'xls' || type === 'xlsx') {
return require("../../../assets/file/excel.png");
} else if (type === 'png' || type === 'gif' || type === 'jpg' || type === 'jpeg') {
return getImageUrl(item.file_path);
} else if (type === 'pdf' || type === 'PDF') {
return require("../../../assets/file/pdf.png");
} else if (type === 'ppt' || type === 'PPT' || type === 'pptx') {
return require("../../../assets/file/ppt.png");
} else if (type === 'txt' || type === 'TXT') {
return require("../../../assets/file/txt.png");
} else if (type === 'zip' || type === 'ZIP' || type === 'rar') {
return require("../../../assets/file/zip.png");
}
return require("../../../assets/file/unknown.png");

},

filepreview(url) {
openFile(getImageUrl(url));




},
onRelateClick() { //修改相关客户
this.$router.push({
name: 'relatelist',
params: {
type: PlateType.Customer,
fromtype: 'taskhandle',
action: 'select',
section: '-1',
row: '-1',
addition: '0'
}
});
},
handleSelectedInfo(data) { //相关列表数据处理
if (data.type === PlateType.Customer) {
this.taskDetail.editCustomer = true;
this.taskDetail.temp_module_id = data.item.id;
this.taskDetail.temp_module_name = data.item.title;
this.$store.commit('updateTaskDetail', this.taskDetail);
} else if (data.type === '4' || data.type === '5') {
if (data.othertype === 'taskviewowner') { //任务下关注人
this.taskDetail.editOwner = true;
this.taskDetail.temp_owner_roles_list = data.data;
this.$store.commit('updateTaskDetail', this.taskDetail);
} else if (data.othertype === 'taskviewabout') { //任务下分配人
this.taskDetail.editAbout = true;
this.taskDetail.temp_about_roles_list = data.data;
this.$store.commit('updateTaskDetail', this.taskDetail);
}
}
},
enterUserDetail(item) { // 查看个人详情
this.$router.push({
name: 'user',
params: {
id: item.role_id
}
})
},
enterCustomerDetail(module_id) {
if (module_id && module_id !== '' && module_id !== '0') {
this.$router.push({
name: 'maindetail',
params: {
id: module_id,
type: PlateType.Customer
}
});
}
},
enterSelectlist(type) { // 查看个人详情
if (type === 'owner') {
var tempIDs = [];
for (var i = 0; i < this.owner_roles_list.length; i++) {
tempIDs.push(this.owner_roles_list[i].role_id);
}
this.$router.push({
name: 'selectlist',
params: {
id: JSON.stringify(tempIDs),
type: 'taskviewowner',
selecttype: '5',
section: '-1',
row: '-1',
}
});
} else if (type === 'about') {
var tempIDs = [];
for (var i = 0; i < this.about_roles_list.length; i++) {
tempIDs.push(this.about_roles_list[i].role_id);
}
this.$router.push({
name: 'selectlist',
params: {
id: JSON.stringify(tempIDs),
type: 'taskviewabout',
selecttype: '4',
section: '-1',
row: '-1',
}
});
}
},
deleteModuleCustomer() { //删除客户关联
AjaxHttp('POST', 'm=task&a=edit', { id: this.$route.params.id, module_id: '', module: '' }, this, function(res, wself, success) {
if (success) {
wself.$vux.toast.text(res.info, 'top');
wself.module_id = '';
wself.module_name = '';
}
});
}
}
}




